.comments {
  padding: 2rem;

  h3 {
    font-size: 1.6rem;
    font-weight: bold;
    margin-bottom: 1.5rem;
  }

  .comment {
    padding-bottom: 2rem;

    &:last-child {
      padding-bottom: 0;
    }
  }

  .user-avatar {
    float: left;
    overflow: hidden;

    img {
      object-fit: cover;
      width: 3rem;
      height: 3rem;
      border-radius: 50%;
    }
  }

  .user-name-comment {
    padding-left: 4.5rem;

    .user-name {
      font-weight: bold;
      font-size: 1.4rem;
      margin-bottom: .3rem;

      p {
        margin-bottom: 0;
      }
    }

    .user-comment {
      p {
        color: #acacac;
        font-size: 1.4rem;
        margin-bottom: 0;
      }
    }
  }

  &-input {
    padding: 0 10px;
    transition: top .4s ease;
    position: relative;
    top: -60px;
    height: 0;
    z-index: 1;

    &.show {
      top: 0;
      height: 60px;
      padding: 10px;
    }

    &-wrapper {
      position: relative;

      textarea {
        background-color: #fff;
        width: 100%;
        padding: 10px 4rem 10px 1rem;
        border-radius: 15rem;
        border: 1px solid #d6ab28;
        line-height: 1;
        height: 4rem;
      }

      .btn-send {
        position: absolute;
        top: 0;
        right: 0;
        background-color: $c-gold;
        border-radius: 50%;
        top: 0;
        right: 0px;
        margin: 5px;

        i {
          padding: 8px;
          font-size: 1.4rem;
          color: $white;
        }
      }
    }
  }

}
