.form-group {
  .item-input {
    padding: 0;
    margin-bottom: .5rem;

    input {
      border-bottom: 1px solid $warm_gray_3;
      background: transparent;
    }

    textarea {
      min-height: 10rem;
      border: 1px solid $warm_gray_3;
      padding: 1rem;
      border-radius: 1rem;
      margin-top: 1rem;
      background-color: transparent;

      &:focus {
        border-color: $c-gold;
      }
    }

    &.item-select {
      margin: 1.5rem 0;
      background: transparent;
      border: 0;
      border-bottom: 1px solid #b3b3b3;
    }

    select {
      background: transparent;
    }
  }

  .input-label {
    color: $warm_gray_3;
    font-size: 1.4rem;

    &-textarea {
      padding-top: 10px;
      display: block;
      color: $warm_gray_3;
    }
  }

  .input-bar:before,
  .input-bar:after {
    content: '';
    height: 1.5px;
    width: 0;
    bottom: 0;
    position: absolute;
    background: $c-gold;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
  }

  .input-bar:before {
    left: 50%;
  }

  .input-bar:after {
    right: 50%;
  }

  /* active state */
  input:focus~.input-bar:before,
  input:focus~.input-bar:after {
    width: 50%;
  }

  /* HIGHLIGHTER ================================== */
  .highlight {
    position: absolute;
    height: 60%;
    width: 100px;
    top: 25%;
    left: 0;
    pointer-events: none;
    opacity: 0.5;
  }

  /* active state */
  input:focus~.highlight {
    -webkit-animation: inputHighlighter 0.3s ease;
    -moz-animation: inputHighlighter 0.3s ease;
    animation: inputHighlighter 0.3s ease;
  }
}
