.video-icons {
  width: 100%;

  .video-status {
    font-size: 1.6rem;
    font-weight: bold;
    color: #969595;
    padding: 2rem 0;
    border-bottom: 1px solid #eee;
  }

  .video-icon-container {
    font-size: 0;
    letter-spacing: 0;
    padding-top: 1rem;
  }

  .video-icon {
    width: 100/3*1%;
    display: inline-block;
    text-align: center;
    font-size: 1.5rem;
    color: #c1c3c7;
    padding: 0 .5rem;

    a {
      cursor: pointer;
      display: inline-block;
      color: #c1c3c7;
      padding: .7rem 0;

      &.active {
        color: #8e8e8e;
      }

      &.like-actived {
        color: #387ef5
      }
    }

    span {
      font-weight: bold;
      margin-left: .5rem;
    }
  }
}
