@font-face {
	font-family: 'Roboto';
	src: url('fonts/roboto/Roboto-Thin.woff2') format('woff2'),
		url('fonts/roboto/Roboto-Thin.woff') format('woff'),
		url('fonts/roboto/Roboto-Thin.ttf') format('ttf');
	font-weight: 300;
	font-style: normal;
}

@font-face {
	font-family: 'Roboto';
	src: url('fonts/roboto/Roboto-Regular.woff2') format('woff2'),
		url('fonts/roboto/Roboto-Regular.ttf') format('ttf'),
		url('fonts/roboto/Roboto-Regular.woff') format('woff');
	font-weight: 400;
	font-style: normal;
}

@font-face {
	font-family: 'Roboto';
	src: url('fonts/roboto/Roboto-Medium.woff2') format('woff2'),
		url('fonts/roboto/Roboto-Medium.ttf') format('ttf'),
		url('fonts/roboto/Roboto-Medium.woff') format('woff');
	font-weight: 500;
	font-style: normal;
}

@font-face {
	font-family: 'Roboto';
	src: url('fonts/roboto/Roboto-Bold.woff2') format('woff2'),
		url('fonts/roboto/Roboto-Bold.woff') format('woff'),
		url('fonts/roboto/Roboto-Bold.ttd') format('ttf');
	font-weight: 700;
	font-style: normal;
}

/* text margin reset */
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol {
	margin-top: 15px;
	font-weight: normal;

	&:first-child {
		margin-top: 0;
	}
}

/* default text styles */
h1 {
	font-family: 'Roboto';
	font-size: 38px;
	line-height: 50px;

	@include media-breakpoint-down(xs) {
		font-size: 30px;
		line-height: 36px;
	}

	&.big {
		font-size: 60px;
		line-height: 72px;
	}
}

h2 {
	font-family: 'Roboto';
	font-size: 26px;
	line-height: 36px;

	@include media-breakpoint-down(xs) {
		font-size: 22px;
		line-height: 32px;
	}
}

h3 {
	font-family: 'Roboto';
	font-size: 20px;
	line-height: 26px;
}

h4 {
	font-family: 'Roboto';
	font-size: 14px;
	line-height: 20px;
}

p,
li {
	font-size: 16px;
	line-height: 24px;
}

strong {
	font-weight: 700;
	font-family: 'Roboto';
}

.black-text {
	color: $black;

	a {
		color: $black;
	}
}

.white-text {
	color: $white;

	a {
		color: $white;
	}
}

.gray-text,
.grey-text {
	color: $text_gray_2;

	a {
		color: $text_gray_2;
	}
}

.green-text {
	color: $stock-up;
}

.red-text {
	color: $stock_down;
}

@mixin bold-text-link {
	font-family: 'latobold';
	font-size: 14px;
	text-align: left;
	line-height: 20px;
	color: $text_black;
}

$TEXT_SIZES: '10',
'12',
'14',
'16',
'20',
'24',
'26';

@each $VALUE in $TEXT_SIZES {

	.t-size-#{$VALUE},
	%t-size-#{$VALUE} {
		font-size: #{$VALUE}px;
	}
}

$TEXT_TRANSFORM: 'uppercase',
'capitalize',
'lowercase';

@each $VALUE in $TEXT_TRANSFORM {

	.t-#{$VALUE},
	%t-#{$VALUE} {
		text-transform: #{$VALUE};
	}
}