.profile {
  &-info {

    .btn-follow{
      color:white;
      text-align: center;
      background: #934787;
    }
    .btn-follow::after{
      content: 'IKUTI';
    }
    .btn-followed{
      color:black;
      text-align: center;
      background: white;
    }
    .btn-followed::after{
      content: 'MENGIKUTI';
    }

    .hero-banner {
      height: 14rem;
    }

    &-user {
      position: relative;
      z-index: 2;
      margin-top: -4rem;
      padding: 0 2.5rem;
      display: flex;

      &-avatar {
        width: 8rem;
        height: 8rem;
        border-radius: 50%;
        border: 2px solid $white;
        overflow: hidden;
        margin-right: 1rem;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

      &-desc {
        flex: 1;

        h2 {
          color: $white;
          font-size: 1.8rem;
        }
      }

      &-action {
        display: flex;

        .action-item {
          padding-top: 1rem;
          padding-bottom: 2rem;
          flex: 1;
          text-align: center;

          h3 {
            margin-bottom: 0;
            font-size: 2rem;
            font-weight: 700;
          }

          sub {
            font-size: 1rem;
            color: $text_gray_2;
          }
        }
      }

      &-button {
        .btn {
          box-shadow: 0 0;
          border-radius: 1rem;
          border: 1px solid rgba($black, .3);
          padding: 1rem 2.5rem;
          font-weight: 500;
          font-size: 1.2rem;
          text-align: center;
        }
      }
    }

    &-caption {
      padding: 2rem 2.5rem 0;

      p {
        font-size: 1.2rem;
        line-height: 1.8rem;
      }
    }

    .button {
      color: $white;
      width: 100%;
    }

    .list {
      padding-bottom: 2rem;
    }
  }
}

.tab-header {
  padding: 2rem 2.5rem 0;

  ul {
    margin: 0;
    padding: 0;
    border: 1px solid rgba($black, .3);
    border-radius: 10px;
    list-style: none;
    display: flex;
    box-shadow: 0px 3px 8.4px 1.6px rgba($black, 0.2);
    overflow: hidden;
  }

  li {
    flex: 1;
    display: inline-block;
    text-align: center;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 1.4rem;

    a {
      padding: .5rem 1rem;
      display: inline-block;
      width: 100%;

      &.active {
        color: $white;
        background-color: $c-gold;
      }
    }
  }
}
