.video-list {
  padding: 1rem 0;
  padding-top: 2rem;

  .container {
    .title {
      padding: 0 1rem;
      margin-bottom: 0;
    }
  }

  .card-list {
    padding: 0;
    margin-bottom: 2rem;

    &-wrapper {
      background-color: $white;
      z-index: 2;
      position: relative;
    }

    &-timeline {
      .card-list-image {
        height: auto;

        .card-list-image-wrapper {
          padding-bottom: 55%;
          height: 0;
        }
      }
    }

    &-image {
      height: 16rem;
    }

    &-small {
      .card-list-image {
        height: 12rem;
      }

      .card-list-wrapper {
        height: 8rem;
      }

      .slick-slide {
        min-width: 18rem;
      }
    }

    &-big {}

    &-user {
      &-info {
        display: flex;
      }

      &-avatar {
        overflow: hidden;
        border-radius: 50%;
        width: 5rem;
        height: 5rem;
        min-width: 5rem;
        margin-right: 1rem;

        a {
          display: inline-block;
        }

        img {
          width: 100%;
          height: 100%;
        }
      }

      &-desc {
        color: $text_gray_2;

        h2 {
          font-weight: bold;
          font-size: 1.8rem;
          margin-bottom: .2rem;
        }
      }

      &-more {
        width: 4rem;
        min-width: 4rem;
        flex: auto;

        button {
          display: flex;
          align-items: center;
          justify-content: flex-end;
          margin-left: auto;
          background-color: transparent;
          border: 0;
          -webkit-appearance: none;

          &:after {
            display: inline-block;
            content: url('../img/icon/ico-more.svg');
          }
        }
      }
    }

    &-footer-icon {
      font-size: 1.2rem;

      &-medium {
        font-size: 1.4rem;
      }
    }
  }

  .slick-slide {
    padding: 0 2rem;
    margin-right: -2rem;
    min-width: 28rem;
  }

  &-full,
  &-full-detail {
    padding-top: 0;

    .card-list {
      margin-bottom: 0;

      &-item {
        margin: 0;

        >a {
          border-radius: 0;
          box-shadow: 0 0;
        }
      }

      &-timeline {
        margin-bottom: 1rem;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    .card-list-wrapper {
      height: auto;
      padding-bottom: .7rem;
    }

    .card-list-user-desc {

      .card-list-user,
      .card-list-view {
        display: inline-block;
      }

      .card-list-user {
        &:after {
          margin-left: 3px;
          content: ' - ';
          display: inline-block;
        }
      }
    }
  }

  &-full-detail {
    &.card-list-image {
      height: 20rem;
    }
  }
}

.video-thumbnail {
  padding-top: 2rem;
  background-color: $white;
  border-top: 1px solid rgba($black, .1);

  p {
    padding: 0 1.5rem;
    color: $text_gray_2;
    font-size: 1.4rem;
    font-weight: 400;
  }

  &-item {}

  .card-list {
    &-item {
      padding: 1.5rem;

      >a {
        display: flex;
      }
    }

    &-image {
      width: 10rem;
      min-width: 10rem;
      height: 10rem;
      overflow: hidden;
      border-radius: 1rem;
    }

    &-wrapper {
      padding: 0;
      margin-left: 1rem;
    }
  }

  .card-list-user-desc {

    .card-list-user,
    .card-list-view {
      display: block;
    }

    .card-list-user {
      &:after {
        content: initial;
      }
    }
  }
}

.card-list {
  padding: 1.5rem 2.5rem;

  &-timeline {
    background-color: $white;

    .video-icons .video-status {
      padding: 1.5rem;
    }

    .video-icons .video-icon-container {
      padding-top: 8px;
      padding-bottom: 8px;
    }
  }

  &-timeline-header {
    padding: 2rem 1.5rem 1.5rem;

    .card-list-user-desc {
      padding-top: 5px;
    }
  }

  &-timeline-status {
    font-size: 1.4rem;
    margin-top: 1.5rem;
    font-weight: 400;

    .max-lines {
      display: block; /* or inline-block */
      text-overflow: ellipsis;
      word-wrap: break-word;
      overflow: hidden;
      max-height: 12em;
      //line-height: 1.8em;
    }

    .no-max-lines {
      display: block; /* or inline-block */
      //text-overflow: ellipsis;
      //word-wrap: break-word;
      //overflow: hidden;
      //max-height: 20em;
      //line-height: 1.8em;
    }
  }

  &-item {
    margin: 1.5rem 0;
    //-box-shadow: 0px 3px 10px 0.52px rgba($black, 0.15);

    >a {
      position: relative;
      display: block;
      background-color: $white;
      box-shadow: 0 4px 15px -2px rgba($black, 0.2);
      border-radius: .5rem;
      overflow: hidden;
    }
  }

  &-image {
    position: relative;
    height: 100%;
    height: 12rem;

    &-wrapper {
      position: relative;
      height: 100%;
      width: 100%;

      background-size: cover;
      background-position: center;
    }
  }

  &-wrapper {
    width: 100%;
    height: 12rem;
    padding: 1.2rem;
    display: flex;
    flex-direction: column;
  }

  &-title,
  &-user-timeline {
    display: block;
    color: $black;
    font-size: 1.4rem;
    line-height: 2rem;
    font-weight: 700;
    text-transform: capitalize;
  }

  &-user {
    font-size: 1.2rem;
  }

  &-date {
    font-size: 1.2rem;
    font-weight: 700;
  }

  &-footer {
    padding-top: 1rem;
    display: flex;
    flex: 1;
    justify-content: flex-end;
    align-items: flex-end;

    &-icon {
      margin-left: 1rem;
      font-size: 1rem;
      color: $warm_gray_3;

      i {
        margin-right: .5rem;
      }
    }

    &-button {
      .btn {
        box-shadow: 0 0;
        border-radius: 1rem;
        border: 1px solid rgba(55, 55, 55, 0.3);
        padding: .2rem 2.5rem;
        font-weight: 500;
        background: transparent;
      }
    }
  }

  &-full {
    padding-bottom: 2.5rem;

    .card-list {
      &-item {
        padding: 0;
        background-color: $white;
        overflow: hidden;
        border-radius: 10px;
      }

      &-image {
        height: 14rem;
      }

      &-tab {
        //background-color: $green;
        display: flex;
        box-shadow: 0px 3px 10px 0.52px rgba($black, 0.15);
        background: #D0A9CD;

        >a {
          flex: 1;
          color: $black;
          font-size: 1.4rem;
          font-weight: 900;
          padding: .5rem 0 .2rem;
          border-bottom: 3px solid transparent;
          text-decoration: none;

          &.active {
            border-color: #650D66;
          }
        }
      }

      &-wrapper {
        height: 24rem;

        &-item {
          display: none;
          overflow: auto;

          &.show {
            display: block;
          }
        }

        h2,
        h3,
        p {
          font-family: 'Roboto';
        }

        h2 {
          font-size: 2.4rem;
          font-weight: 700;
        }

        h3 {
          font-size: 1.8rem;
          line-height: 1rem;
          font-weight: 500;
          color: $text_gray_2;
        }

        p {
          font-size: 1.4rem;
          line-height: 2rem;
        }

        &-button {
          padding: 2.5rem 0 1.5rem;
        }
      }
    }
  }
}

.profile-card-list {
  .container {
    display: none;

    &.show {
      display: block;
    }
  }

  .card-list {
    padding-left: 1rem;
    padding-right: 1rem;
    margin-top: 0px;
  }

  .card-list-item{
    margin-top: 0px;
  }
}

.notification-list {
  .card-list {
    &-item {
      display: flex;
      margin-bottom: 2rem;
    }

    &-icon {
      margin-top: .3rem;
      margin-right: 1rem;
      width: 2rem;
    }

    &-desc {
      border-bottom: 1px solid rgba($black, .3);
      flex: 1;

      p {
        margin-top: 0;
        font-size: 1.4rem;
      }
    }

    &-title {
      h4 {
        display: inline;
        font-weight: 700;
      }

      span {
        font-weight: 400;
      }

      &.new {
        h4 {
          color: $c-love;
        }
      }
    }

    &-date {
      font-size: 1rem;
      color: $text_gray_2;
      margin-bottom: 1rem;
    }
  }
}


.videos-swipe {
  background-color: $white;
  padding: 1.5rem 1.5rem 1.5rem;
  margin-bottom: 1rem;

  &-title {
    font-weight: 700;
    font-size: 1.8rem;
  }

  .videos-swipe-scrollover {
    margin-left: -1.5rem;
    margin-right: -1.5rem;
  }

  &-lists {
    white-space: nowrap;
    overflow-x: scroll;
    webkit-overflow-scrolling: touch;
    @extend .cf;
    padding-left: 1.5rem;
  }

  &-item {
    display: inline-block;
    vertical-align: top;
    margin-right: 1rem;
    width: 27.5vw;
    position: relative;
    padding-bottom: 21.5%;
    border-radius: 6px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;

    &:last-child {
      margin-right: 1.5rem;
    }
  }

  &-item-title {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    text-align: center;
    background-image: linear-gradient(-180deg, rgba($black, 0) 0%, rgba($black, .75) 100%);

    h5 {
      text-align: center;
      position: absolute;
      bottom: 1rem;
      margin: 0;
      padding: 0 8px;
      color: $white;
      text-overflow: ellipsis;
      overflow: hidden;
      width: 100%;
    }
  }
}

.top-talent{
  h3{
    font-weight: 700;
    font-size: 1.8rem;
  }
  background-color: $white;
  padding: 1.5rem 1.5rem 1.5rem;
  margin-bottom: 1rem;

  .item-wrapper{
    display: flex;
    flex-direction: column;

    .button{
      height: 1em;
    }
    .list{
      height: auto;
      padding:0;
      margin:0;
    }
    .item{
      border: none;
    }
    .item-detail{
      &-name{
        font-weight: 700;
      }
      &-hobby{

      }
    }
  }
}

.project-swipe {
  h3{
    font-weight: 700;
    font-size: 1.8rem;
  }
  background-color: $white;
  padding: 1.5rem 1.5rem 1.5rem;
  margin-bottom: 1rem;

  .item-wrapper{
    height: 100%;
    width: 100%;
    display: flex;
    white-space: nowrap;
    overflow-x: scroll;
    webkit-overflow-scrolling: touch;
    @extend .cf;
    //padding-left: 1.5rem;
  }

  .item-thumbnail{
    margin-right: 1rem;
    width: 37.5vw;
    height: 37.5vw;
    background-size: cover;
    background-repeat:   no-repeat;
    background-position: center center;
    border-radius: 10px;
  }

  .item-desc{
    font-weight: bold;
  }
}


.program-timeline {
  background-color: $white;
  padding: 2rem 1.5rem 0.5rem;
  margin-bottom: 1rem;

  &-title {
    font-weight: 700;
    font-size: 1.6rem;
  }

  &-list {
    padding-bottom: 1.5rem;
  }

  &-item {
    display: block;
    position: relative;
    min-height: 65px;
    margin-bottom: 1.5rem;
    padding-left: 7.5rem;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &-image {
    position: absolute;
    left: 0;
    width: 65px;
    height: 65px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
  }

  &-description {
    font-size: 1.4rem;
    line-height: 1.5;
  }

  &-more {
    padding: 1rem;
    margin-left: -1.5rem;
    margin-right: -1.5rem;
    text-align: center;
    position: relative;
    color: $c-gold;

    h4 {
      margin: 0;
      font-size: 1.6rem;
      line-height: 1.8;
      font-weight: 700;
      color: $c-gold;
    }

    &-icon {
      position: absolute;
      top: 50%;
      right: 1.5rem;
      transform: translateY(-50%);

      i {
        font-size: 2rem;
        line-height: 2;
      }
    }
  }
}
