ion-side-menus{
  .menu-content{
    box-shadow:inset -1px 0px 8px rgba(0, 0, 0, .1), 1px 0px 8px rgba(0, 0, 0, 0.1);
  }
}


.side-menu{
  .list{
    padding:2.5rem;
    height: 100%;
    .parent{
      display: flex;
      height: 75%;
      justify-content: flex-end;
    }
    .child{
      align-self: flex-end;
    }
  }
  .item{
    border:0;
    padding:0;
  }
  .user-panel{
    position:relative;
    padding-left: 70px;
    min-height: 55px;
    padding-bottom:10px;
    margin-bottom: 10px;
    &-avatar{
      position:absolute;
      left: 0;
      width: 55px;
      height: 55px;
      border-radius:50%;
      overflow:hidden;
      background-repeat:no-repeat;
      background-position:center center;
      background-size:cover;
    }
    &-desription{
      padding:5px 0;
    }
    &-name{
      color:$green_dark;
      font-size:1.6rem;
      margin-bottom: 5px;
      font-weight: 500;
    }
    &-data{
      @extend .cf;
      &-item{
        float: left;
        font-size:1.3rem;
        margin-right:15px;
        color:$warm_gray_3;
        &:last-child{
          margin-right: 0;
        }
      }
      i{
        margin-right: 5px;
      }
    }
  }
  i.fas,i.far{
    color:$warm_gray_3;
  }
  ion-item{
    &:not(.user-panel){
      .item-content,
      &.user-logout{
        // background:red;
        padding: 15px 20px 15px 35px;
        position:relative;
        color:$text_gray;
        &:hover{
          color:$green_dark;
        }
      }
      &.user-logout{
        margin-top: 20px;
      }
      i{
        position:absolute;
        left: 5px;
        top: 50%;
        margin-right: 10px;
        font-size:1.4rem;
        transform:translateY(-50%);
      }
    }
  }
}
