.bar-dark {
  &.bar-positive {
    background-color: $black;
  }

  .title {
    font-family: 'Roboto';
    font-weight: 400;
    font-size: 1.8rem;
  }

  .button {
    border: 0;
    min-width: 3rem;

    img {
      vertical-align: middle;
    }
  }
}

.bar-purple{
  background: rgba(155,4,147,1);
  background: -moz-linear-gradient(45deg, rgba(155,4,147,1) 0%, rgba(77,6,144,1) 100%);
  background: -webkit-gradient(left bottom, right top, color-stop(0%, rgba(155,4,147,1)), color-stop(100%, rgba(77,6,144,1)));
  background: -webkit-linear-gradient(45deg, rgba(155,4,147,1) 0%, rgba(77,6,144,1) 100%);
  background: -o-linear-gradient(45deg, rgba(155,4,147,1) 0%, rgba(77,6,144,1) 100%);
  background: -ms-linear-gradient(45deg, rgba(155,4,147,1) 0%, rgba(77,6,144,1) 100%);
  background: linear-gradient(45deg, rgba(155,4,147,1) 0%, rgba(77,6,144,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#9b0493', endColorstr='#4d0690', GradientType=1 );
  .title-center{
    color:$white;
  }
  .back-button{
    color:$white;
  }
}

.bar-gold{
  background-color:$c-gold-3;
  .title-center{
    color:$white;
  }
  .back-button{
    color:$white;
  }
}

.platform-android {
  .bar-dark {
    .title {
      font-size: 1.8rem;
    }
  }
}

.button-burger{
  background-color:transparent;
  position:relative;
  &.active, &.activated{
    border-color:transparent;
    background-color:transparent;
  }
  .bars-icon,.angle-icon{
    line-height: 1.5;
    color:$white;
    transition:all .3s ease;
    i{
      font-size: 2rem;
    }
  }
  .bars-icon{
    transform:rotate(0deg) scale(1);
  }
  .angle-icon{
    position:absolute;
    top: 2px;
    left:10px;
    transform:rotate(-90deg) scale(0);
    i{
      font-size: 2.4rem;
    }
  }
}

.search-form{
  position:absolute;
  z-index: 99;
  left: 15px;
  right: 60px;
  top: 0;
  padding:5px 0;
  &.search-on-content{
    position:relative;
    left: auto;
    right: auto;
    padding:0 1.5rem;
    .input-text{
      height: 36px;
      color:$black;
      border:solid 1px rgba($black, .2);
    }
    i{
      left: 2.8rem;
      top: 1rem;
      color:rgba($black, .2);
    }
  }
  i{
    position:absolute;
    font-size: 1.6rem;
    color:$white;
    left: 1.2rem;
    top: 1.3rem;
  }
  .input-text{
    width: 100%;
    height: 32px;
    border:solid 1px $white;
    background-color:transparent;
    border-radius:20px;
    color:$white;
    padding:.5rem 1.5rem .5rem 3.5rem;
  }
}

.menu-open{
  .button-burger .bars-icon{
    transform:rotate(90deg) scale(0);
  }
  .button-burger .angle-icon{
    transform:rotate(0deg) scale(1);
  }
}
