.kitchen-sink {
  padding: 0 1rem;

  h1, h2, h3, h4, h5, h6 {
    font-weight: bold;
  }

  h1 {
    font-size: 2.6rem;
  }

  h2 {
    font-size: 2.4rem;
  }

  h3 {
    font-size: 2.2rem;
  }

  h4 {
    font-size: 2rem;
  }

  ul {
    list-style-type: disc;
    ul {

    }
  }

  ol {
    list-style-type: decimal;
    ol {

    }
  }

  ul, ol {
    padding-left: 2rem;
    margin-bottom: 1.5rem;
    li {
      padding-bottom: 1rem;
      &:last-child {
        padding-bottom: 0;
      }
    }
  }
}