.flash-slider {
  position: relative;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4rem 2.5rem;
  background: rgba(0, 0, 0, .8), url(../img/bg/bg-splash.png);
  background-position: center;
  background-size: cover; 

  &-wrapper {
    position: relative;
    z-index: 1041;
    border-radius: 10px;
    background-color: $white;
    overflow: hidden;
    min-width: 24rem;
    height: 100%;
    width: 100%;
    flex: 1;

    h1 {
      color: $c-gold-dark;
      font-weight: 700;
      font-family: 'Exo', sans-serif;
      font-size: 2.2rem;
      line-height: 1.3;
    }

    p {
      font-size: 1.6rem;
      line-height: 1.3;
      color: $black;
    }
  }

  &-item {
    text-align: center;
    display: flex;
    flex-direction: column;
    flex: 1;
    height: 100%;
    position: relative;
    background-color: $white;

    &-image {
      flex: 1;
      height: 16rem;
      align-items: center;
      justify-content: center;
      display: flex;

      img {
        margin: 0 auto;
        max-height: 100%;
      }
    }

    &-wrapper {
      padding: 1.5rem;
    }

    &-footer {
      position: absolute;
      bottom: 5.5rem;
      padding: 1rem 0;
      left: 0;
      width: 100%;
      text-align: center;

      a {
        //-font-size: 2rem;
        color: $text_gray_2;
        font-size: 1.5rem;
        font-weight: 500;

        &.btn-gold {
          color: $c-gold-dark;

          span,
          i {
            vertical-align: middle;
            font-size: 1.5rem;
            //-font-size: 2rem;
          }

          i {
            margin-left: .5rem;
            font-size: 2rem;
            // top: .2rem;
            // position: relative;
          }
        }
      }
    }
    &.show{
      .flash-slider-item-image{
        opacity: 1;
        transform:translateY(0) rotateY(0deg);
      }
      .flash-slider-content,
      .flash-slider-item-footer{
        opacity: 1;
        transform:translateY(0);
      }
    }
  }

  .slider-pager {
    bottom: 3rem;
    height: auto;
    transition:all .5s ease;
    opacity:0;
    transform:translateY(4px);
    transition-delay:.25s;
    &.show{
      opacity:1;
      transform:translateY(0);
    }
    .slider-pager-page {
      color: $c-gold-dark;
      margin: 0 1rem;
      font-size: 1.2rem;
    }
  }

  &-item-image{
    opacity:0;
    transform:translateY(8px) rotateY(90deg);
    transition:all .65s ease;
  }
  &-content,
  &-item-footer{
    opacity:0;
    transition:all .5s ease;
    transition-delay:.35s;
  }
  &-content{
    margin-top: 15px;
    transform:translateY(-8px);
  }
  &-item-footer{
    transform:translateY(8px);
  }

  &-first{
    height: 100%;
    .flash-slider-item-image{
      position:relative;
      transition:all .65s ease;
      opacity:0;
      transform:translateY(21vh);
    }
    .flash-slider-content{
      margin-top: 15px;
    }
    .flash-slider-content,
    .flash-slider-item-footer{
      opacity:0;
      transition:all .5s ease;
      transition-delay:.25s;
    }
    .flash-slider-content{
      transform:translateY(-8px);
    }
    .flash-slider-item-footer{
      transform:translateY(8px);
    }
    &.show-logo{
      .flash-slider-item-image{
        opacity:1;
        transform:translateY(20vh);
      }
    }
    &.show{
      .flash-slider-item-image{
        transform:translateY(0.5vh);
      }
      .flash-slider-content,
      .flash-slider-item-footer{
        opacity:1;
        transform:translateY(0);
      }
    }
  }

}

.modal-icon {
  align-items: center;
  justify-content: center;

  &.modal-animation {
    .modal-icon-animation {
      .animate {
        display: block;
      }
    }

    .modal-icon-text {
      opacity: 1;
    }
  }

  &-wrapper {
    position: relative;
    z-index: 1041;
    border-radius: 10px;
    overflow: hidden;
    min-width: 24rem;
  }

  &-animation {
    transform: scale(1.5);
    transform-origin: top;
  }

  &-text {
    opacity: 0;
    transition: opacity .1s ease-in;
    text-align: center;
    position: relative;
    top: -4rem;

    h2 {
      color: $white;
    }
  }
}