.pulse-animate{
  background: linear-gradient(-90deg, #ccc 0%, #eee 50%, #ccc 100%);
  background-size: 400% 400%;
  animation: pulse 1.2s linear infinite;
}
// video-list-skeleton
.video-list-skeleton{
    .image{
        width: 100%;
        height: 16rem;
        @extend .pulse-animate;
    }
    .description{
        padding: 1.2rem 1.2rem 1.5rem 7.2rem;
        position:relative;
    }
    .avatar{
        position:absolute;
        left: 1.2rem;
        top: 1.2rem;
        border-radius: 50%;
        width: 5rem;
        height: 5rem;
        min-width: 5rem;
        @extend .pulse-animate;
    }
    .title{
        margin-top: 4px;
        height: 1.8rem;
        width: 50%;
        margin-bottom: 8px;
    }
    .title,
    .caption{
        border-radius:4px;
        @extend .pulse-animate;
    }
    .caption{
        width: 90%;
        height: 1.4rem;
    }
    .bottom-section{
      padding:1.2rem 1.5rem;
    }
    .views{
      width: 50%;
      height: 1.6rem;
      border-radius:4px;
      @extend .pulse-animate;
    }
    .icons{
      text-align: center;
      margin-top: 2rem;
      padding-top: 1.7rem;
      border-radius:4px;
      border-top: solid 1px rgba($black, .1);
    }
    .icon-item{
      border-radius:4px;
      width: 20%;
      margin:0 4%;
      height: 1.6rem;
      display:inline-block;
      @extend .pulse-animate;
    }
}

// video-detail-skeleton
.video-detail-skeleton{
  .video-section{
    background-color:$white;
    .frame{
        position: relative;
        padding-bottom: 56.25%;
        padding-top: 30px;
        height: 0;
        @extend .pulse-animate;
    }
    .description{
        padding: 1.2rem 1.2rem 1.5rem 7.2rem;
        position:relative;
    }
    .avatar{
        position:absolute;
        left: 1.2rem;
        top: 1.2rem;
        border-radius: 50%;
        width: 5rem;
        height: 5rem;
        min-width: 5rem;
        @extend .pulse-animate;
    }
    .title{
        margin-top: 4px;
        height: 1.8rem;
        width: 50%;
        margin-bottom: 8px;
    }
    .title,
    .caption{
        border-radius:4px;
        @extend .pulse-animate;
    }
    .caption{
        width: 90%;
        height: 1.4rem;
    }
    .bottom-section{
      padding:1.2rem 1.5rem;
    }
    .views{
      width: 50%;
      height: 1.6rem;
      border-radius:4px;
      @extend .pulse-animate;
    }
    .icons{
      text-align: center;
      margin-top: 2rem;
      padding-top: 1.7rem;
      border-radius:4px;
      border-top: solid 1px rgba($black, .1);
    }
    .icon-item{
      border-radius:4px;
      width: 20%;
      margin:0 4%;
      height: 1.6rem;
      display:inline-block;
      @extend .pulse-animate;
    }
  }
  .comment-secion{
    padding: 2rem;
    .comment-title{
      width: 50%;
      height: 1.8rem;
      border-radius:4px;
      @extend .pulse-animate;
    }
    .comment-item{
      padding: 1.2rem 1rem 1.2rem 5rem;
      position:relative;
    }
    .avatar{
        position:absolute;
        left: 0;
        top: 1.4rem;
        border-radius: 50%;
        width: 3.5rem;
        height: 3.5rem;
        min-width: 3.5rem;
        @extend .pulse-animate;
    }
    .title{
        margin-top: 4px;
        height: 1.6rem;
        width: 50%;
        margin-bottom: 8px;
    }
    .title,
    .caption{
        border-radius:4px;
        @extend .pulse-animate;
    }
    .caption{
        width: 100%;
        height: 1.2rem;
    }
  }
  .next-video{
    padding: 2rem 1.5rem;
    background-color:$white;
    &.search-page{
      padding-top: 1.5rem;
    }
    .next-video-title{
      width: 50%;
      height: 1.8rem;
      border-radius:4px;
      margin-bottom: 2rem;
      @extend .pulse-animate;
    }
    .next-video-item{
      position:relative;
      padding-left: 11.5rem;
      min-height:10rem;
      margin-bottom: 3rem;
      &:last-child{
        margin-bottom: 0;
      }
      .image{
        position:absolute;
        left: 0;
        width: 10rem;
        min-width: 10rem;
        height: 10rem;
        overflow: hidden;
        border-radius: 1rem;
        @extend .pulse-animate;
      }
      .title{
        margin-top: 4px;
        height: 1.8rem;
        width: 70%;
        margin-bottom: 8px;
      }
      .title,
      .caption,
      .views{
        border-radius:4px;
        @extend .pulse-animate;
      }
      .caption{
        width: 40%;
        height: 1.4rem;
        margin-bottom: 1rem;
      }
      .views{
        width: 60%;
        height: 1.2rem;
      }
    }
  }
}

// landing-header-skeleton
.landing-header-skeleton{
  padding: 1.2rem 1.5rem;
  margin-bottom: 1rem;
  background-color:$white;
  .head{
    height: 48px;
    border-radius:8px 8px 0 0;
    border:solid 1px #eee;
    background-color:#f3f3f3;
    .title{
      height: 25px;
      width: 60%;
      min-width:180px;
      margin:10px auto;
      border-radius:4px;
      @extend .pulse-animate;
    }
  }
  .categories{
    background-color:#f8f8f8;
    height: 95px;
    padding:0 10px;
    border-right:solid 1px #eee;
    border-bottom:solid 1px #eee;
    border-left:solid 1px #eee;
    border-radius:0 0 8px 8px;
  }
  .skeleton-item{
    margin: 0;
    border:0;
    width: 20%;
    float:left;
    padding:15px 12px;
    background-color:transparent;
    .circle{
      width: 100%;
      padding-bottom: 100%;
      position:relative;
      border-radius:50%;
      margin-bottom: 10px;
      @extend .pulse-animate;
    }
    .title{
      width: 100%;
      height: 10px;
      border-radius:2px;
      @extend .pulse-animate;
    }
  }
}

// videos-swipe-skeleton
.videos-swipe-skeleton{
  padding:1.5rem;
  margin-bottom: 1rem;
  background-color:$white;
  .title{
    max-width:180px;
    height: 20px;
    border-radius:4px;
    margin-bottom: 1.5rem;
    @extend .pulse-animate;
  }
  .lists{
    white-space:nowrap;
  }
  .scrollover{
    margin-left: -1.5rem;
    margin-right: -1.5rem;
  }
  .lists{
    white-space: nowrap;
    overflow-x: scroll;
    webkit-overflow-scrolling: touch;
    padding-left: 1.5rem;
    @extend .cf;
  }
  .skeleton-item{
    display: inline-block;
    vertical-align: top;
    margin-right: 1rem;
    width: 27.5vw;
    position: relative;
    padding-bottom: 21.5%;
    border-radius: 6px;
    @extend .pulse-animate;
    &:last-child {
      margin-right: 1.5rem;
    }
  }
}

