.article-list-skeleton{
  padding:2rem;
  .skeleton-item{
    max-width: 50rem;
    width: 100%;
    height: 90vh;
    margin: 0 auto 2rem;
    border-radius: 5px;
    @extend .pulse-animate;
  }
}

.category-list-skeleton{
  padding:2rem;
  .skeleton-item{
    max-width: 50rem;
    width: 100%;
    height: 9rem;
    margin: 0 auto 2rem;
    border-radius: 5px;
    @extend .pulse-animate;
  }
}

.category-detail-skeleton{
  padding:3rem 2.5rem;
  .image{
    height: 14rem;
    border-radius:10px 10px 0 0;
    @extend .pulse-animate;
  }
  .tab-control{
    padding:8px 0 2px;
    text-align: center;
    background-color:$white;
    position:relative;
    box-shadow:0 3px 10px 0.52px rgba(55,55,55,.15);
  }
  .tab-item{
    border-radius:4px;
    width: 25%;
    margin:0 2.5%;
    height: 1.6rem;
    display:inline-block;
    @extend .pulse-animate;
  }
  .tab-content{
    padding:1.2rem;
    background-color:$white;
    border-radius:0 0 10px 10px;
  }
  .text{
    height: 1.4rem;
    border-radius:2px;
    margin-bottom: 8px;
    @extend .pulse-animate;
    &:nth-child(2),
    &:nth-child(4),
    &:nth-child(8){
      width: 85%;
    }
    &:nth-child(10){
      width: 70%;
    }
    &:nth-child(3),
    &:nth-child(6),
    &:nth-child(9){
      width: 90%;
    }
  }
  .act-button{
    height: 3.6rem;
    box-shadow:0 0 10px 0 rgba(0,0,0,.2);
    border-radius:1rem;
    width: 10rem;
    margin:3rem auto;
    @extend .pulse-animate;
  }
}

.profile-info-skeleton{
  .hero-banner{
    height: 14rem;
    @extend .pulse-animate;
  }
  .info{
    position: relative;
    z-index: 2;
    margin-top: -4rem;
    padding: 0 2.5rem;
    display: flex;
  }
  .avatar{
    width: 8rem;
    height: 8rem;
    border-radius: 50%;
    border: 2px solid $white;
    margin-right: 1rem;
    @extend .pulse-animate;
  }
  .description{
    padding:8px 0;
    flex:1;
    .name{
      width: 80%;
      min-width:180px;
      border:solid 1px rgba($white, .25);
      height: 1.8rem;
      border-radius:4px;
      @extend .pulse-animate;
    }
  }
  .statistic{
    @extend .cf;
    margin-top: 3rem;
  }
  .skeleton-item{
    float: left;
    width: 33.33%;
    padding:0 8px;
    text-align: center;
  }
  .skeleton-number{
    display:inline-block;
    width: 30px;
    height: 2.2rem;
    border-radius:4px;
    margin-bottom:5px;
    @extend .pulse-animate;
  }
  .skeleton-label{
    display:inline-block;
    width: 60px;
    height: 1.2rem;
    border-radius:4px;
    @extend .pulse-animate;
  }
  .button-edit{
    margin-top: 2rem;
    height: 3.4rem;
    border-radius:8px;
    @extend .pulse-animate;
  }
  .caption{
    padding:1.5rem 2.5rem 0;
    .text{
      height: 1.4rem;
      border-radius:2px;
      margin-bottom: 8px;
      @extend .pulse-animate;
      &:nth-child(1){
        width: 95%;
      }
      &:nth-child(3){
        width: 85%;
      }
      &:nth-child(4){
        width: 90%;
      }
      &:nth-child(5){
        width: 30%;
      }
    }
  }
}

.profile-card-list-skeleton{
  padding:2rem;
  .box{
    position: relative;
    box-shadow: 0 4px 15px -2px rgba(55,55,55,.2);
    border-radius: .5rem;
    overflow: hidden;
    background-color:$white;
    margin-bottom: 3.5rem;
  }
  .image{
    height: 12rem;
    @extend .pulse-animate;
  }
  .description-text{
    height: 12rem;
    padding: 1.2rem;
    position:relative;
  }
  .title{
    height: 1.8rem;
    border-radius:4px;
    margin-bottom: 8px;
    @extend .pulse-animate;
    width: 90%;
    &:nth-child(2){
      width: 60%;
    }
  }
  .icon{
    height: 1.6rem;
    width: 80px;
    border-radius:4px;
    position:absolute;
    right: 1.2rem;
    bottom: 1.2rem;
    @extend .pulse-animate;
  }
}

@keyframes pulse {
    0% {
        background-position: 0% 0%
    }
    100% {
        background-position: -135% 0%
    }
}
