.category-list {
  padding: 1rem;
  display: flex;
  justify-content: space-around;

  &-full {
    flex-direction: column;
    margin: 1rem;

    .category-list {
      &-item {
        max-width: 50rem;
        width: 100%;
        margin: 0 auto 2rem;
        border-radius: 5px;
        overflow: hidden;

        &:last-child {
          margin-bottom: 0;
        }
      }

      &-image,
      &-image-wrapper {
        position: relative;
        width: 100%;
        height: 100%;
      }

      &-image-wrapper {;
        background-size: cover;
        display: flex;
        justify-content: center;
        align-items: center;

        h3 {
          position: initial;
          width: 100%;
          font-size: 1.8rem;
          text-align: center;
        }
      }
    }
  }

  &-item {
    //padding: 1.5rem 0;
    width: 5rem;
    height: 9rem;
    border-radius: .5rem;
    overflow: hidden;

    a {
      position: relative;
      display: block;
      background-color: $c-gold;
      //border-radius: .5rem;
      height: 100%;

      &:nth-child(2) {}
    }
  }

  &-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    background-size: contain;
    background-repeat: no-repeat;
  }

  &-icon {
    flex: 1;
    display: flex;
    align-items: flex-end;

    img {
      height: 60%;
      max-height: 12rem;
    }
  }

  &-title {
    display: block;
    font-weight: 500;
    padding-top: 1rem;
    padding-bottom: .5rem;
    font-size: 1.3rem;
    color: $white;
    text-transform: capitalize;
    position: absolute;
    bottom: 0;
  }
}
