.tabs {
  background-color: $white;
  box-shadow: 0px 3px 8.4px 1.6px rgba($black, 0.45);
  background-image: none;

  .tab-item {
    .icon {
      background-size: auto;

      &.nu-home {
        background-repeat: no-repeat;
        background-position: center center;
        height: 100%;
        //-        background-size: 30px 30px;
        background-image: url('../img/icon/ico-home.svg');
      }

      &.nu-search {
        background-repeat: no-repeat;
        background-position: center center;
        height: 100%;
        //-background-size: 30px 30px;
        background-image: url('../img/icon/ico-search.svg');
      }

      &.nu-gallery {
        background-repeat: no-repeat;
        background-position: center center;
        height: 100%;
        //-background-size: 30px 30px;
        background-image: url('../img/icon/ico-gallery.svg');
      }

      &.nu-notif {
        background-repeat: no-repeat;
        background-position: center center;
        height: 100%;
        //-background-size: 30px 30px;
        background-image: url('../img/icon/ico-notification.svg');
      }

      &.nu-user {
        background-repeat: no-repeat;
        background-position: center center;
        height: 100%;
        //-background-size: 30px 30px;
        background-image: url('../img/icon/ico-user.svg');
      }
    }
  }
}

.tabs-footer {
  .tabs {
    background-color: #ebebeb;
    //box-shadow: 0 -3px 5px rgba(0,0,0,0.15);
    box-shadow: 0px 3px 8.4px 1.6px rgba($black, 0.45);
  }
}
