html {
  height: 100%;
}

body {
  display: flex;
  flex: 1;
  min-height: 100%;
  //font-size: 12px;

  &.page-dashboard {
    display: block;
  }
}

body,
html {
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
  font-size: 10px;
  color: $black;
  position: relative;
}

body.platform-android5 {
  //font-size: 10px;
}

// add max width 1920px to the whole page
body {

  margin: 0 auto;
  position: relative;

  >div[class^="page-"] {
    width: inherit;
    margin: 0 auto;
    position: relative;
    overflow: hidden;

    background: #fff;
    // background: $maybak-yellow;
  }
}

/* predefined link style */
a {
  color: $black;
}

a,
span {
  &.see-more {
    // display:inline-block !important;
    // height:auto !important;
    // font-size:14px !important;
    // text-transform:uppercase;
    // font-family:'latobold';
    // line-height:21px !important;
    // text-decoration:none !important;
    // padding-right:16px !important;
    // background:none !important;
    cursor: pointer;
    display: inline-block;
    height: auto;
    font-size: 14px;
    text-transform: uppercase;
    font-family: 'latobold';
    line-height: 21px;
    text-decoration: none;
    padding-right: 16px;
    background: none;

    position: relative;

    &::after {
      content: '';
      display: block;
      width: 10px;
      height: 10px;
      margin: auto;
      position: absolute;
      background: url('../img/icons/arrow-see-more.png') center center no-repeat;
      right: 0;
      top: 0;
      bottom: 1px;
      -webkit-transition: right 0.4s;
      -ms-transition: right 0.4s;
      transition: right 0.4s;
    }

    &:hover {
      color: $black;

      &::after {
        /* right:-4px; */
        -webkit-animation: bouncingArrow 1s linear 0s both;
        animation: bouncingArrow 1s linear 0s both;
      }
    }
  }
}

ul,
ol {
  padding-left: 15px;

  // level 2
  ul {
    list-style: square inside url('data:image/gif;base64,R0lGODlhBQAKAIABAAAAAP///yH5BAEAAAEALAAAAAAFAAoAAAIIjI+ZwKwPUQEAOw==');
  }
}

.content {
  position: relative;
  z-index: 1;

  &.hasBG {
    position: relative;

    &:before {
      //-content: '';
      width: 100%;
      height: 100%;
      position: fixed;
      background-image: url(../img/bg/bg-dashboard.png);
    }
  }
}

.title {
  &-line {
    position: relative;
    margin-bottom: 1rem;

    h2 {
      margin-bottom: 0;
      font-weight: 700;
    }

    &:after {
      display: inline-block;
      background-color: $c-gold;
      content: '';
      width: 5rem;
      height: 2px;
      position: relative;
      top: -.5rem;
    }

  }
}

//overide
.pane {
  background: $c-grey-light2;
}

.video-list {
  padding: 1rem 0;
  padding-top: 2rem;

  .container {
    .title {
      padding: 0 1rem;
      margin-bottom: 0;
    }
  }

  .card-list {
    padding: 0;
    margin-bottom: 2rem;

    .card-list-wrapper {
      background-color: $white;
    }

    .card-list-image {
      height: 16rem;
    }

    .card-list-embed-video {
      height: auto;
    }

    .card-list-small {
      .card-list-image {
        height: 12rem;
      }

      .card-list-wrapper {
        height: 8rem;
      }

      .slick-slide {
        min-width: 18rem;
      }
    }

    .card-list-big {}

    .card-list-user {
      .card-list-user-info {
        display: flex;
      }

      .card-list-user-avatar {
        overflow: hidden;
        border-radius: 50%;
        width: 5rem;
        height: 5rem;
        min-width: 5rem;
        margin-right: 1rem;

        img {
          width: 100%;
          height: 100%;
        }
      }

      .card-list-user-desc {
        color: $text_gray_2;

        h2 {
          font-weight: bold;
          font-size: 1.8rem;
          margin-bottom: .2rem;
        }
      }

      &-more {
        width: 4rem;
        min-width: 4rem;
        flex: auto;

        button {
          display: flex;
          align-items: center;
          justify-content: flex-end;
          margin-left: auto;
          background-color: transparent;
          border: 0;
          -webkit-appearance: none;

          &:after {
            display: inline-block;
            content: url('../img/icon/ico-more.svg');
          }
        }
      }
    }

    &-footer-icon {
      font-size: 12rem;

      &-medium {
        font-size: 1.4rem;
      }
    }
  }

  .slick-slide {
    padding: 0 2rem;
    margin-right: -2rem;
    min-width: 28rem;
  }

  &-full,
  &-full-detail {
    padding-top: 0;

    .card-list {
      margin-bottom: 0;

      &-item {
        margin: 0;

        >a {
          border-radius: 0;
          box-shadow: 0 0;
        }
      }
    }

    .card-list-wrapper {
      padding-bottom: 2rem;
    }

    .card-list-user-desc {

      .card-list-user,
      .card-list-view {
        display: inline-block;
      }

      .card-list-user {
        &:after {
          margin-left: 3px;
          content: ' - ';
          display: inline-block;
        }
      }
    }
  }

  &-full-detail {
    &.card-list-image {
      height: 20rem;
    }
  }
}

.video-thumbnail {
  padding-top: 2rem;
  background-color: $white;
  border-top: 1px solid rgba($black, .1);

  p {
    padding: 0 1.5rem;
    color: $text_gray_2;
    font-size: 1.4rem;
    font-weight: 400;
  }

  &-item {}

  .card-list {
    &-item {
      padding: 1.5rem;

      >a {
        display: flex;
      }
    }

    &-image {
      width: 10rem;
      min-width: 10rem;
      height: 10rem;
      overflow: hidden;
      border-radius: 1rem;
    }

    &-wrapper {
      padding: 0;
      margin-left: 1rem;
    }
  }

  .card-list-user-desc {

    .card-list-user,
    .card-list-view {
      display: block;
    }

    .card-list-user {
      &:after {
        content: initial;
      }
    }
  }
}

.card-list {
  padding: 1.5rem 2.5rem;

  &-item {
    margin: 1.5rem 0;
    //-box-shadow: 0px 3px 10px 0.52px rgba($black, 0.15);

    >a {
      position: relative;
      display: block;
      background-color: $white;
      box-shadow: 0 4px 15px -2px rgba($black, 0.2);
      border-radius: .5rem;
      overflow: hidden;
    }
  }

  &-image {
    position: relative;
    height: 100%;
    height: 12rem;

    &-wrapper {
      position: relative;
      height: 100%;
      width: 100%;

      background-size: cover;
      background-position: center;
    }
  }

  &-wrapper {
    width: 100%;
    height: 12rem;
    padding: 1.2rem;
    display: flex;
    flex-direction: column;
  }

  &-title {
    display: block;
    color: $black;
    font-size: 1.4rem;
    line-height: 2rem;
    font-weight: 700;
    text-transform: capitalize;
  }

  &-user {
    font-size: 1.2rem;
  }

  &-footer {
    padding-top: 1rem;
    display: flex;
    flex: 1;
    &-icon {
      margin-left: 1rem;
      font-size: 1rem;
      color: $warm_gray_3;

      i {
        margin-right: .5rem;
      }
    }

    &-button {
      .btn {
        box-shadow: 0 0;
        border-radius: 1rem;
        border: 1px solid rgba(55, 55, 55, 0.3);
        padding: .2rem 2.5rem;
        font-weight: 500;
        background: transparent;
      }
    }
  }

  &-full {
    padding-bottom: 2.5rem;

    .card-list {
      &-item {
        padding: 0;
        background-color: $white;
        overflow: hidden;
        border-radius: 10px;
      }

      &-image {
        height: 14rem;
      }

      &-tab {
        //background-color: $green;
        display: flex;
        box-shadow: 0px 3px 10px 0.52px rgba($black, 0.15);

        & > a {
          flex: 1;
          color: $black;
          font-size: 1.4rem;
          font-weight: 900;
          padding: .5rem 0 .2rem;
          border-bottom: 3px solid transparent;
          text-decoration: none;
          cursor: pointer;

          &.active {
            border-color: $c-gold;
          }
        }
      }

      &-wrapper {
        height: 24rem;

        &-item {
          display: none;
          overflow: auto;

          &.show {
            display: block;
          }
        }

        h2,
        h3,
        p {
          font-family: 'Roboto';
        }

        h2 {
          font-size: 2.4rem;
          font-weight: 700;
        }

        h3 {
          font-size: 1.8rem;
          line-height: 1rem;
          font-weight: 500;
          color: $text_gray_2;
        }

        p {
          font-size: 1.4rem;
          line-height: 2rem;
        }

        &-button {
          padding: 2.5rem 0 1.5rem;
        }
      }
    }
  }
}

.profile-card-list {
  .container {
    display: none;

    &.show {
      display: block;
    }
  }

  .card-list {
    padding-left: 1rem;
    padding-right: 1rem;
  }
}

.notification-list {
  .card-list {
    &-item {
      display: flex;
      margin-bottom: 2rem;
    }

    &-icon {
      margin-top: .3rem;
      margin-right: 1rem;
      width: 2rem;
    }

    &-desc {
      border-bottom: 1px solid rgba($black, .3);
      flex: 1;

      p {
        margin-top: 0;
        font-size: 1.4rem;
      }
    }

    &-title {
      h4 {
        display: inline;
        font-weight: 700;
      }

      span {
        font-weight: 400;
      }

      &.new {
        h4 {
          color: $c-love;
        }
      }
    }

    &-date {
      font-size: 1rem;
      color: $text_gray_2;
      margin-bottom: 1rem;
    }
  }
}


.hasBanner {
  .hero-banner {
    position: fixed;
    top: 7rem;
    z-index: 10;
    width: 100%;
  }

  .category-list {
    padding-top: 21.5rem;
  }
}

.hero-banner {
  background-color: $c-gold;
  height: 14rem;

  &-wrapper {
    height: 14rem;
    position: relative;
  }

  &-image {
    position: relative;
    height: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 14rem;

    h2 {
      display: flex;
      height: 100%;
      justify-content: center;
      align-items: center;
      color: white;
      font-weight: 900;
      margin-bottom: 0;
      text-align: center;
    }
  }
}

.swiper-container {
  position: relative;
  margin-bottom: 0;

  &:before {
    display: block;
    width: 100%;
    height: 100%;
    z-index: 10;
    //content: '';
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba($black, .3);
    pointer-events: none;
  }

  .slick-list {
    height: 100%;

    .slick-track {
      height: 100%;
    }
  }
}

.slick-dots {
  bottom: 0;
  padding: 3rem 0 1rem;
  background: linear-gradient(to bottom, rgba($black, 0) 0%, rgba($black, 0.35) 100%);

  li {
    padding: 0 1.5rem;

    &.slick-active {
      button {
        &:before {
          color: $green;
          opacity: 1;
        }
      }
    }

    button {
      &:before {
        color: $green;
        font-size: 1.4rem;
      }
    }
  }
}

.container {
  .form-group {
    padding: 4rem 2rem;
  }
}
