.landing-header {
  padding: 1.2rem 1.5rem;
  background-color:$white;
  margin-bottom: 1rem;
  .landing-header-inner {
    //background: $c-gold-2;
    background: rgba(190,45,125,1);
    background: -moz-linear-gradient(45deg, rgba(190,45,125,1) 0%, rgba(77,6,144,1) 100%);
    background: -webkit-gradient(left bottom, right top, color-stop(0%, rgba(190,45,125,1)), color-stop(100%, rgba(77,6,144,1)));
    background: -webkit-linear-gradient(45deg, rgba(190,45,125,1) 0%, rgba(77,6,144,1) 100%);
    background: -o-linear-gradient(45deg, rgba(190,45,125,1) 0%, rgba(77,6,144,1) 100%);
    background: -ms-linear-gradient(45deg, rgba(190,45,125,1) 0%, rgba(77,6,144,1) 100%);
    background: linear-gradient(45deg, rgba(190,45,125,1) 0%, rgba(77,6,144,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#be2d7d', endColorstr='#4d0690', GradientType=1 );
    border-radius: 8px;
    color: #fff;
    overflow:hidden;
  }

  .avatar-tagline {
    @extend .cf;
    //background-color:$c-gold-3;
    background: rgba(155,4,147,1);
    background: -moz-linear-gradient(-45deg, rgba(155,4,147,1) 0%, rgba(77,6,144,1) 100%);
    background: -webkit-gradient(left top, right bottom, color-stop(0%, rgba(155,4,147,1)), color-stop(100%, rgba(77,6,144,1)));
    background: -webkit-linear-gradient(-45deg, rgba(155,4,147,1) 0%, rgba(77,6,144,1) 100%);
    background: -o-linear-gradient(-45deg, rgba(155,4,147,1) 0%, rgba(77,6,144,1) 100%);
    background: -ms-linear-gradient(-45deg, rgba(155,4,147,1) 0%, rgba(77,6,144,1) 100%);
    background: linear-gradient(135deg, rgba(155,4,147,1) 0%, rgba(77,6,144,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#9b0493', endColorstr='#4d0690', GradientType=1 );
    position:relative;
    padding:1rem 1.5rem;
    text-align: center;
    .img-container {
      width: 30px;
      height: 30px;
      position:absolute;
      top: 1rem;
      left: 1.5rem;
      border-radius:50%;
      overflow:hidden;
      img{
        width: 100%;
        height: 100%;
        object-fit:cover;
      }
    }
    h1 {
      font-size: 1.8rem;
      line-height: 1.6;
      color: inherit;
      margin: 0;
      font-weight: 700;
    }
  }

  .main-categories {
    padding: 2rem .8rem 1.5rem;
    ul {
      padding: 0;
      margin: 0;
      font-size: 0;
      letter-spacing: 0;
    }
    li {
      width: 100/5*1%;
      display: inline-block;
      text-align: center;
      a {
        display: inline-block;
        max-width: 6rem;
        width: 100%;
      }
      h2 {
        font-size: 14px;
        line-height: 1.5;
        color: #fff;
        font-weight: 700;
        margin: 8px 0 0;
      }
    }
  }
}
