.popup-container .popup {
  border-radius: 1rem;
}

.popup {
  background-color: $white;
  overflow: hidden;

  &-head {
    padding-bottom: 0;
  }

  &-title {
    font-size: 1.4rem;
    font-weight: 500;
    font-family: 'Exo', sans-serif;
  }

  &-body {
    font-size: 1.3rem;
    padding: 1rem 2rem;
  }

  &-buttons {
    padding: 0;
    padding-top: 1.5rem;
    min-height: 5.5rem;
  }

  .button {
    text-transform: uppercase;
    //border-radius:8px;
    transition: .2s;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    min-height: 0;
    line-height: 1;
    margin: 0;
    box-shadow: 0 0;
    border: 0;
    font-size: 1.4rem;

    &.button-positive {
      background: $c-gold-dark;
    }
  }

  &-error {
    .button {
      background-color: $c-gmail;
    }
  }
}


.action-sheet {
  .button {
    padding: .5rem 2rem;
    border-bottom: 1px solid rgba(0, 0, 0, .1);
  }
}

.platform-android {
  .action-sheet {
    .button {
      text-align: center;
    }
  }
}
