.page-landing {
  .content {
    width: 100%;
  }

  .title {
    padding: 2rem 0 3rem;

    h1 {
      margin: 0 auto;
      color: $green_dark;
      font-family: 'Roboto';
      font-weight: 300;
      text-transform: uppercase;
    }
  }

  .form {
    &-group {
      margin: 0 auto;
      max-width: 36rem;
      padding: 0 2rem;
    }

    &-button-group {
      .btn {
        margin: 1.5rem 0;
        border-radius: 1rem;
        font-weight: 500;
        font-size: 1.4rem;
      }
    }

    &-caption {
      //color: $warm_gray_3;
      color: #020000;
      padding-bottom: 2rem;

      p {
        font-weight: 400;
        font-size: 1.2rem;
        line-height: 1.2;
      }

      a {
        color: #020000;
        font-weight: 500;
      }
    }
  }
}

.landing {
  &-logo {
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      padding-top: 50px;
      width: auto;
    }

    &-top {
      height: 14rem;
      margin-bottom: 20px;
      margin-top: 20px;
      // background-image: url(../img/bg/bg-default-top.png);
      // background-size: 100% 150px;
      // background-repeat: no-repeat;
      // background-position: top center;
    }

    &-wrapper {
      margin: 0 auto;
      // background-color: $white;
      // width: 9rem;
      // height: 9rem;
      // box-shadow: 0px 6px 15px 0px rgba($green,0.3);
      // border-radius: 2rem;

      img {
        padding-top: 0;
        height: 80%;
        margin: 0 auto;
        display: inline-block;
      }
    }
  }

  // Gate
  &-gate {
    .form-group {
      padding: 0 1rem;
    }

    .btnSocmed{
      height: 5em;
    }

    .separator-or {
      border-bottom: solid 1px $background_gray;
      position: relative;
      margin: 1rem 0 2.5rem;

      span {
        position: absolute;
        display: inline-block;
        padding: 2px 8px;
        line-height: 1;
        font-size: 1.1rem;
        top: -6px;
        left: 50%;
        transform: translateX(-50%);
        background-color: $c-grey-light2;
      }
    }
  }

  &-gate-title {
    text-align: center;
  }

  &-footer {
    height: 320px;
    //-background-image: url(../img/bg/bg-landing.png);
    background-size: 100% 300px;
    background-repeat: no-repeat;
    background-position: bottom center;
    position: absolute;
    bottom: 0;
    width: 100%;
    display: flex;
    align-items: flex-end;

    &-button {
      width: 100%;
      // max-width: 25rem;
      // margin: 0 auto;
      // padding: 5rem 0;

      margin: 0 auto;
      max-width: 36rem;
      padding: 5rem 2rem;
    }

    .btn {
      display: inline-block;

      &:first-child {
        margin-bottom: 1rem;
      }
    }
  }
}

.greeting{
  .container{
    display: flex;
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
  }

  .greeting-1{
    align-self: center;
    margin: 10px;
    width: 100%;

    .close-icon{
      float: right;
      right: 0;
      width: 25px;
    }
    .font-white{
      color: white;
    }
    .cat-selected{
      background: #E1E2E4;
    }
    .separator-or {
      border-bottom: solid 1px $background_gray;
      position: relative;
      margin: 10px;
    }
    .header{
      color: #A58A23;
      text-align: center;
      font-size: 1.5em;
    }
    .footer{
      color: #A58A23;
      text-align: center;
      font-size: 1.5em;
    }
    .icon-size{
      width: 50px;
      align-self: center;
      align-items: flex-end;
    }
    .desc{
      font-size: 1.5em;
      align-self: center;
      align-items: center;
      text-align: left;
    }
    .col-wrapper{
      display: flex;
      width: 100%;
      justify-content: center;
    }

    .col-wrapper-2{
      display: flex;
      width: 100%;
      justify-content: flex-start;
    }
    .row{
      width: 90%;
      margin-top: 10px;
    }
    .video{
      width: 100%;
      height: auto;
      text-align: center;
    }
  }
  .main-background{
    background: rgba(155,4,147,1);
    background: -moz-linear-gradient(45deg, rgba(155,4,147,1) 0%, rgba(77,6,144,1) 100%);
    background: -webkit-gradient(left bottom, right top, color-stop(0%, rgba(155,4,147,1)), color-stop(100%, rgba(77,6,144,1)));
    background: -webkit-linear-gradient(45deg, rgba(155,4,147,1) 0%, rgba(77,6,144,1) 100%);
    background: -o-linear-gradient(45deg, rgba(155,4,147,1) 0%, rgba(77,6,144,1) 100%);
    background: -ms-linear-gradient(45deg, rgba(155,4,147,1) 0%, rgba(77,6,144,1) 100%);
    background: linear-gradient(45deg, rgba(155,4,147,1) 0%, rgba(77,6,144,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#9b0493', endColorstr='#4d0690', GradientType=1 );
  }
}
