/** Generic Buttons */

.btn {
  text-transform: uppercase;
  padding: 1rem 2rem;
  //border-radius:8px;
  border-radius: .5rem;
  transition: .2s;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  min-height: 0;
  line-height: 1;
  font-size: 1.4rem;
  border-radius: 1rem;

  &.no-transition {
    transition: none;
  }

  &-block {
    display: block;
  }

  &.wide {
    padding-left: 4rem;
    padding-right: 4rem;
  }

  &.addon {
    padding-right: 6rem;
    padding-left: 1rem;
    position: relative;
    //width:100%;
    text-align: left;

    img,
    svg,
    .symbol {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 7%;
      width: 12px;

      /* exception for premier-wealth button, which arrow
               change color when hovered =___= */
      &.hover {
        display: none;
      }
    }

    .symbol {
      font-size: 17px;
      width: auto;
    }

    &.free-img-size {

      img,
      svg {
        width: auto;
      }
    }
  }

  &.addon-reverse {
    padding-right: 1rem;
    padding-left: 6rem;
    position: relative;
    //width:100%;
    text-align: right;

    img,
    svg,
    .symbol {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 7%;
      width: 12px;
    }

    .symbol {
      font-size: 17px;
      width: auto;
    }
  }

  &.no-shadow {
    box-shadow: 0 0 0 0;
  }

  &.btn-grey {
    background-color: $footer_gray;
  }
}

.btn-link {
  box-shadow: none;
  padding: 0 !important;

  &.addon img {
    position: relative;
    top: 0; // make sure you check the line height of the text if you want to change this.
    transform: none;
    right: -10px;
  }

  &.btn-green {
    color: $green;
  }

  &.btn-grey {
    background-color: transparent;
    color: $warm_gray_2;
  }

  &.btn-white {
    color: $white;
  }
}

// button green
.btn-primary {
  cursor: pointer;
  color: $white;
  background-color: $green;
  border-color: $green;
}

// button white
.btn-secondary {
  color: $green;
  background-color: $white;
  border-color: $white;
}

// button green
.btn-tertiary {
  color: #fff;
  background-color: $c-gold;
  border-color: $c-gold;
}

// button gmail
.btn-gmail {
  cursor: pointer;
  color: $white;
  background-color: $c-gmail;
  border-color: $c-gmail;
  text-transform: uppercase;
}

// button gmail
.btn-facebook {
  cursor: pointer;
  color: $white;
  background-color: $c-facebook;
  border-color: $c-facebook;
  text-transform: uppercase;
}

// button twitter
.btn-twitter {
  cursor: pointer;
  color: $white;
  background-color: $c-twitter;
  border-color: $c-twitter;
  text-transform: uppercase;
}


/** addons */

.btn.addon--small {
  @extend .addon;
  padding-right: 3rem;
}

.btn.addon-reverse--small {
  @extend .addon-reverse;
  padding-left: 3rem;
}

// custom width and height for pin icons
.btn.addon-reverse img.ico-pin,
.btn.addon img.ico-pin {
  width: 20px;
  height: 20px;
}


/** Specific Buttons */

.btn-back {
  width: 100%;
  max-width: 100px;
  padding-left: 0 !important;
  border-color: transparent;
  color: $white;

  #arrow-left {
    fill: $white;
  }

  @media screen and (min-width:768px) {
    border-color: $white;
  }

  @media screen and (max-width:767px) {
    box-shadow: none;
  }

  .text {
    visibility: hidden;

    @media screen and (min-width: 768px) {
      visibility: visible;
    }
  }

  img,
  svg {
    left: 17% !important;
  }
}

.btn-outline {
  &.btn-secondary {
    color: $white;
    border-color: $white;
    background-color: transparent;
  }

  &.btn-tertiary {
    color: $c-gold;
    border-color: $c-gold;
    background-color: transparent;
  }
}

.btn-hide-modal {
  padding: 7px 1rem;

  .ion {
    color: $c-gold-dark;
    font-size: 2.2rem;
  }
}
